.LeftButton{
    color: #ffffff;
    position: fixed;
    left: 0;
    top: 50%;
    z-index: 1;
    cursor: pointer;
    -webkit-transition: color 0.3s ease-out 0s;
    -o-transition: color 0.3s ease-out 0s;
    transition: color 0.3s ease-out 0s;
}

.RightButton {
    color: #ffffff;
    position: fixed;
    right: 0;
    top: 50%;
    z-index: 1;
    cursor: pointer;
    -webkit-transition: color 0.3s ease-out 0s;
    -o-transition: color 0.3s ease-out 0s;
    transition: color 0.3s ease-out 0s;
}

.RightButton:hover, .LeftButton:hover {
    color: #cccccc;
}

.InputForm {
    margin-right: 160px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.FormGroup {
    -ms-flex-item-align: end;
        align-self: flex-end;
}



