.MovieItem {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 300px;
    height: 75px;
    margin-bottom: 15px;
    border-radius: 23px;
    -webkit-box-shadow: 0 10px 10px #ececec;
            box-shadow: 0 10px 10px #ececec;
    color: #ececec;
    cursor: pointer;
    -webkit-transition: all 0.3s ease-out 0s;
    -o-transition: all 0.3s ease-out 0s;
    transition: all 0.3s ease-out 0s;
}

.MovieItem:hover {
    color: #000;
    background-color: #ececec;
}


